@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.full{
    width: 100vw;
    height: 100vh;
    background-color: #030303;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding-top: 20vh;
}
.center{
    width: auto;
    height: auto;
}

.logo{
    width: 100%;
    height: auto;
    padding: 0px 0px 20px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.logo-img{
    width: 30px;
    height: 30px;
    margin-right: 5px;
}
.logo p{
    font-family: "anton";
    font-size: 1.5em;
    font-family: 600;
    color: white;
}

.desc{
    width: 100%;
    height: auto;
    padding: 20px 30px;
    margin-top: 20px;
}
.desc p{
    font-family: "montserrat";
    font-weight: 500;
    color: white;
    font-size: 1em;
    margin-top: 60px;
    text-align: center;
}
.desc p:nth-child(1){
    margin-top: 0px;
    font-size: 2em;
}